@import "./components/components.fonts";
@import "./components/components.mixins";
@import "./components/components.general";
@import "./components/components.header";
@import "./components/components.footer";

* {
  margin: 0;
  padding: 0;
  outline: none;
  text-decoration: none;
  -webkit-box-sizing: border-box;
  box-sizing: border-box;
  border-collapse: collapse;
}

$blue: #47436a;
$red: #ff6827;

img {
  user-select: none;
  -moz-user-select: none;
  -ms-user-select: none !important;
  -webkit-user-select: none !important;
}

a {
  color: inherit;
  cursor: pointer;
}
a:focus {
  outline: none;
}
button {
  cursor: pointer;
}

.container {
  max-width: 1140px;
  margin: 0 auto;
}

body {
  font-family: $font;
  font-size: 18px;
  line-height: 165%;
  font-weight: 400;
  font-style: italic;
  color: $blue;
}

.overflow {
  position: relative;
  overflow: hidden;
}
input,
select,
textarea,
a,
button {
  font-family: $font;
}

// -   OFFER*****************************************************

.offer {
  background: url(../img/1_bg1.png) 0% 0% no-repeat;
  background-size: cover;
  padding: 50px 35px 120px 35px;
  position: relative;
}
.offer__container {
  position: relative;
  z-index: 5;
}

.offer__sale-desc {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  position: absolute;
  left: 340px;
  top: 7px;
  font-weight: 600;
  font-size: 30px;
  line-height: 37px;
  color: #73919b;
  div {
    margin-right: 50px;
    width: 150px;
  }
}
@media all and (-ms-high-contrast: none), (-ms-high-contrast: active) {
  .offer__sale-desc {
    width: 1200px;
  }
}
.offer__sale-mobile {
  font-weight: 600;
  font-size: 30px;
  line-height: 37px;
  text-align: right;
  color: $red;
  display: none;
}
.offer__img-mobile {
  display: none;
}
.section-title_big {
  font-weight: 600;
  font-size: 44px;
  line-height: 61px;
  max-width: 565px;
  margin-top: 50px;
  margin-bottom: 30px;
  position: relative;
  z-index: 20;
  span {
    color: $red;
  }
}
.subtitle {
  max-width: 430px;
  margin-bottom: 30px;
}
.price {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: flex-start;
  margin-bottom: 20px;
  max-width: 250px;
}
.old-price {
  width: 100%;
  font-size: 24px;
  line-height: 29px;
  text-decoration-line: line-through;
  align-items: flex-end;
  text-align: right;
}
@media all and (-ms-high-contrast: none), (-ms-high-contrast: active) {
  .old-price {
    position: relative;
  }
  .old-price:before {
    content: "";
    display: block;
    background-color: $blue;
    width: 120px;
    height: 2px;
    position: absolute;
    right: -5px;
    top: 15px;
    z-index: 20;
  }
  .product4 .old-price:before {
    width: 100px;
    right: -2px;
    top: 15px;
  }
  .end .old-price:before {
    background-color: white;
  }
}

@media (max-width: 480px) and (min-width: 300px) and (-ms-high-contrast: none),
  (max-width: 480px) and (min-width: 300px) and (-ms-high-contrast: active) {
  .old-price:before {
    width: 105px;
    right: -2px;
    top: 15px;
  }
  .product4 .old-price:before {
    width: 90px;
    left: -2px;
    top: 12px;
  }
}

.new-price {
  width: 100%;
  font-weight: 600;
  color: $red;
  font-size: 30px;
  line-height: 40px;
  span {
    font-size: 50px;
    line-height: 61px;
  }
  margin-top: -22px;
}
.btn-order {
  display: inline-block;
  width: 250px;
  height: 60px;
  background: #ff6827;
  border-radius: 45px;
  font-size: 18px;
  line-height: 60px;
  color: #ffffff;
  margin-bottom: 30px;
  text-align: center;
  z-index: 20;
  transition: all ease-out 0.3s;
}
.btn-order:hover {
  background: $blue;
}
.products .btn-order:hover {
  background: #73919b;
}
.duration-sale {
  font-size: 16px;
  line-height: 20px;
}
.offer__video-wrapper a {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  color: #ffffff;
  position: absolute;
  left: 360px;
  bottom: 130px;
}
.offer__video-icon {
  margin-right: 20px;
}
.offer__abs-img-big {
  position: absolute;
  left: 470px;
  top: 100px;
  pointer-events: none;
}
.offer__abs-img-small {
  position: absolute;
  right: -390px;
  top: -10px;
  pointer-events: none;
}
.offer__abs-img-dots {
  position: absolute;
  left: -250px;
  top: 0;
  pointer-events: none;
}
.offer__abs-cloud1 {
  position: absolute;
  left: 200px;
  top: -100px;
  pointer-events: none;
}
.offer__abs-clouds {
  position: absolute;
  left: 10%;
  bottom: -340px;
  transform: scale(1.6) !important;
}

@media (max-width: 1680px) {
  .offer__abs-clouds {
    position: absolute;
    left: -5%;
    bottom: -215px;
    transform: scale(1) !important;
  }
}

// -  advantages *****************************************************

.advantages {
  padding: 0px 35px 0px 35px;
  margin-top: -50px;
  z-index: 2;
  position: relative;
}
.advantages__container {
  position: relative;
}
.advantages__list {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: flex-start;
  position: relative;
  z-index: 20;
}
.advantages__item {
  width: 32%;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: flex-start;
}
.advantages__item_icon {
  width: 80px;
  height: 80px;
  margin-right: 20px;
}
.advantages__item-wrapper {
  width: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
.advantages__item_title {
  font-weight: 600;
  font-size: 24px;
  line-height: 29px;
  margin-bottom: 10px;
  text-align: left;
  width: 100%;
}
.advantages__item_text {
  width: 100%;
}
.advantages__wrapper-slider {
  max-width: 500px;
  padding: 75px 75px 0px 75px;
  position: relative;
}
.advantages__slider {
  max-width: 350px;
  width: 100%;
}
.advantages__slide_img {
  margin-bottom: 95px;
}
.nav-btn {
  width: 30px;
  height: 30px;
  border-radius: 50%;
  background: #ffffff;
  border: 1px solid #73919b;
  box-sizing: border-box;
  color: #73919b;
  transition: all ease-out 0.3s;
  font-size: 35px;
  font-style: normal;
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  cursor: pointer;
}
.nav-btn-prev {
  transform: rotate(-10deg);
  padding-right: 4px;
  padding-bottom: 5px;
}
.nav-btn-next {
  transform: rotate(-10deg);
  padding-bottom: 5px;
}
.advantages__prev {
  position: absolute;
  left: 60px;
  top: 250px;
  z-index: 20;
}
.advantages__next {
  position: absolute;
  right: 60px;
  top: 250px;
  z-index: 20;
}
.nav-btn:hover {
  background: #73919b;
  color: white;
}
.advantages__img {
  margin-top: 50px;
}
.advantages__wrapper {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: flex-start;
  margin-top: 20px;
}
.advantages__wrapper-bg {
  position: absolute;
  left: 0;
  top: 0;
  z-index: 10;
}
.advantages__wrapper-bg_mobile {
  display: none;
}

.advantages__button {
  cursor: pointer;
  width: 30px;
  height: 30px;
  border-radius: 50%;
  border: 1px solid #ffffff;
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  transition: all ease-out 0.3s;
  background: inherit;
  div {
    width: 18px;
    height: 18px;
    border-radius: 50%;
    background: #ffffff;
  }
}
.advantages__wrapper-img {
  position: relative;
}
.advantages__button1 {
  position: absolute;
  top: 425px;
  right: 87px;
}
.advantages__button2 {
  position: absolute;
  top: 225px;
  left: 530px;
}
.advantages__button3 {
  position: absolute;
  top: 90px;
  left: 390px;
}
.advantages__button4 {
  position: absolute;
  top: 215px;
  left: 335px;
}
.advantages__button5 {
  position: absolute;
  top: 380px;
  left: 105px;
}
.slick-active .advantages__button {
  width: 50px;
  height: 50px;
  border: 1px solid $red;
  div {
    width: 30px;
    height: 30px;
    background: $red;
  }
}
.advantages__slide_title {
  font-weight: 600;
  font-size: 24px;
  line-height: 29px;
  margin-bottom: 10px;
  width: 100%;
}
.advantages__slide_text {
  width: 100%;
}

@media all and (-ms-high-contrast: none), (-ms-high-contrast: active) {
  .advantages__button {
    border: 1px solid #ffffff;
    background: #73919b;
    div {
      background: #ffffff;
    }
  }
  .slick-active .advantages__button {
    border: 1px solid $red;
    background: #ffffff;
    div {
      background: $red;
    }
  }
}
.advantages__abs-img-man1 {
  position: absolute;
  left: -35px;
  top: -35px;
  pointer-events: none;
}
.advantages__abs-img-man2 {
  position: absolute;
  right: -60px;
  top: 0px;
  pointer-events: none;
}
.advantages__abs-cloud2 {
  position: absolute;
  left: -100px;
  top: 250px;
  pointer-events: none;
}
.advantages__abs-cloud3 {
  position: absolute;
  left: 50px;
  top: 600px;
  pointer-events: none;
}
.advantages__abs-img-dots {
  position: absolute;
  left: -40px;
  top: 50px;
  transform: rotate(90deg);
  pointer-events: none;
}
.advantages__abs-cloud4 {
  position: absolute;
  left: -140px;
  top: 200px;
  transform: scale(0.5);
  pointer-events: none;
}
.advantages__abs-cloud5 {
  position: absolute;
  left: 345px;
  top: 120px;
  transform: scale(0.5);
  pointer-events: none;
}

// -  video *****************************************************

.video {
  padding: 100px 35px 70px 35px;
}
.video__container {
  display: flex;
  flex-direction: row;
  justify-content: flex-start;
  align-items: flex-start;
  position: relative;
}
.video__left {
  width: 40%;
  margin-right: 3%;
}
.video__right {
  width: 40%;
  position: relative;
  z-index: 10;
}
.video__wrapper {
  cursor: pointer;
  width: 470px;
  height: 300px;
  margin: 0 auto;
  border-radius: 20px;
  overflow: hidden;
  display: flex;
  justify-content: center;
  align-items: center;
  // position: relative;
  background-image: url(../img/3_video_img.png);
  background-size: cover;
  background-repeat: no-repeat;
  background-position: 50% 50%;

  iframe {
    width: 100%;
    height: 100%;
  }
}
.play-icon {
  cursor: pointer;
  transition: all ease-out 0.3s;
}
.video__wrapper:hover .play-icon {
  transform: scale(1.2);
}
.section-title {
  font-weight: 600;
  font-size: 30px;
  line-height: 37px;
  margin-bottom: 30px;
}
.video .section-title {
  margin-top: 70px;
}
.video__item {
  margin-bottom: 20px;
  margin-left: 40px;
  position: relative;
}
.video__item::before {
  content: url(../img/icons/check.svg);
  display: block;
  width: 20px;
  height: 20px;
  position: absolute;
  left: -40px;
  top: 5px;
}
.video__abs-img-dots1 {
  position: absolute;
  left: 425px;
  top: -170px;
  transform: rotate(90deg);
  pointer-events: none;
}
.video__abs-img-dots2 {
  position: absolute;
  right: -190px;
  top: -35px;
  pointer-events: none;
}

