// -  products *****************************************************

.products {
  // background: url(../img/5_bg.png) 0% 25% no-repeat;
  // background-size: cover;
  // background-repeat: no-repeat;
  padding: 50px 35px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  position: relative;
}
.product {
  padding: 45px 50px;
  margin-bottom: 20px;
  background: #ffffff;
  border-radius: 15px;
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: stretch;
  flex-wrap: wrap;
  max-width: 990px;
  width: 100%;
  position: relative;
  z-index: 10;
}
.product__title_mobile {
  display: none;
}
.product__left {
  width: 40%;
}
.product__rate {
  display: inline-block;
  padding: 5px 30px 5px 50px;
  margin-left: -50px;
  margin-bottom: 20px;
  background: #73919b;
  border-radius: 0px 12.5px 12.5px 0px;
}

.stars__out {
  position: relative;
  color: grey;
  z-index: 1;
}
.stars__in {
  position: absolute;
  z-index: 2;
  color: #ffc224;
  font-size: inherit;
  display: block;
  top: 0;
  left: 0;
  bottom: 0;
  overflow: hidden;
  width: 0;
}
.product__section-title {
  font-weight: 600;
  font-size: 24px;
  line-height: 29px;
  margin-bottom: 10px;
}
.product__price {
  display: flex;
  flex-direction: row;
  justify-content: flex-start;
  align-items: center;
  max-width: none;
  margin-bottom: 10px;
}
.product__old-price {
  width: auto;
  font-size: 24px;
  line-height: 29px;
  margin-right: 20px;
  align-items: none;
  text-align: left;
  display: inline-block;
}
.product__new-price {
  width: auto;
  margin: 0;
  display: inline-block;
  span {
    font-size: 50px;
    line-height: 61px;
  }
}
.product__description_table {
  width: 100%;
  font-size: 14px;
  line-height: 25px;
  td {
    width: 155px;
  }
}
td.product__description_param {
  width: 85px;
}
.product__description {
  display: flex;
  flex-direction: row;
  justify-content: flex-start;
  align-items: stretch;
  width: 100%;
  font-size: 14px;
  line-height: 25px;
}
.product4__img_mobile {
  display: none;
}
.product4__logo {
  display: none;
}
.product__description-left {
  width: 145px;
}
.product__description-right {
  width: 155px;
}
.product .slick-slide img {
  display: block;
  width: 100%;
}

.product__right {
  width: 60%;
  position: relative;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: flex-end;
}
.product__logo {
  position: absolute;
  left: 0;
  top: 0;
}
.product3__logo {
  position: absolute;
  left: 70px;
  top: 0;
}
.product__slider-large {
  width: 80%;
}
.product__slider-mini {
  width: 20%;
  max-width: 130px;
  a {
    margin-top: 20px;
  }
}
.product__prev {
  position: absolute;
  left: 58%;
  bottom: 0;
  z-index: 20;
}
.product__next {
  position: absolute;
  right: 22%;
  bottom: 0;
  z-index: 20;
}
.product__sale {
  max-width: 125px;
  font-weight: 600;
  font-size: 48px;
  line-height: 54px;
  text-align: right;
  color: $red;
  position: absolute;
  top: 4%;
  right: 22%;
}
.product__center {
  width: 100%;
  margin-top: 30px;
}
.product__order-form {
  width: 100%;
  display: flex;
  flex-direction: row;
  justify-content: flex-start;
  align-items: center;
}
.order__input,
.product__size_select {
  width: 230px;
  height: 45px;
  background-color: white;
  border: 1px solid #47436a;
  border-radius: 22.5px;
  padding: 10px 20px;
  font-size: 14px;
  line-height: 25px;
  margin-right: 10px;
  transition: all ease-out 0.3s;
}
.product__size_select {
  width: 150px;
  -webkit-appearance: none;
  -moz-appearance: none;
  -ms-appearance: none;
  appearance: none !important;
}
select::-ms-expand {
  display: none;
}
.order__input:focus,
.product__size_select:focus {
  border: 1px solid #ff6827;
}
.product__size {
  position: relative;
}
.product__size::after {
  content: url(../img/icons/arrow-bottom.svg);
  display: block;
  width: 12px;
  height: 5px;
  position: absolute;
  right: 25px;
  top: 7px;
  z-index: 20;
  pointer-events: none;
}
.btn-buy {
  width: 250px;
  height: 45px;
  border: none;
  background: #ff6827;
  border-radius: 45px;
  font-weight: 600;
  font-size: 18px;
  line-height: 45px;
  color: #ffffff;
  margin-bottom: 0px;
}
.product__sale-block {
  background: url(../img/5_bg_sale.png) 0% 0% no-repeat;
  background-size: cover;
  background-repeat: no-repeat;
  padding: 20px 170px;
  font-weight: 600;
  font-size: 18px;
  line-height: 30px;
  color: #ffffff;
  border-radius: 10px;
  margin-bottom: 40px;
  span {
    font-size: 26px;
    margin-left: 5px;
    display: inline-block;
